const initial = {
  pagination: {
    current: 1,
    pageSize: 10
  },
  filterResult: {},
  colletion: { name: "", note: "" }
};

const index = (state = initial, action) => {
  switch (action.type) {
    case "SET_KEKE_COLLECTION":
      let res = Object.assign({}, state, action.value);
      return res;
    default:
      return state;
  }
};
export default index;
